@import "../../assets/stylesheet/helpers/custom-variables";

.profile {
  height: 100%;

  &__title {
    position: relative;
    z-index: 10;
    display: inline-block;
    &::before {
      content: "";
      background-color: $secondary;
      position: absolute;
      height: 1.313rem;
      left: 0;
      right: 0;
      top: 45%;
      z-index: -1;
      opacity: 0.2;
    }
  }

  &__sidebar {
    width: 18.813rem;

    @media screen and (max-width: 992px) {
      width: 100%;
    }

    &__user {
      #ar & {
        border-top-right-radius: 10px;
      }

      #en & {
        border-top-left-radius: 10px;
      }
    }

    .list-group {
      .list-group-item-action {
        border-bottom: 0;
        &.active {
          background-color: lighten($primary, 48%);

          #ar & {
            border-right: 0.25rem solid $secondary;
          }

          #en & {
            border-left: 0.25rem solid $secondary;
          }
        }
      }
    }
  }

  &__history {
    border: 1px solid rgba($primary, 0.14);

    h5 {
      border-bottom: 1px solid rgba($primary, 0.14);
    }
  }

  &__list {
    border: 0;

    .list-group-item {
      border: 1px solid rgba($primary, 0.14);
      padding: 1rem;
      border-radius: $border-radius;
    }
  }

  &__favorite {
    &__image {
      width: 12.813rem;
      @media screen and (max-width: 767px) {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
  }
}
